
*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.Layout {
  --panel-pad: 2rem;
  --aside-width: max(37vw, 16rem);
  display: flex;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: trench, sans-serif;
  color: var(--foreground-color);
  background-color: var(--background-color);
}

.Layout-main {
  overflow: hidden;
  flex-grow: 1;
  margin: var(--panel-pad);
}

.Layout-sidebar {
  position: relative;
  width: 0;
  padding: var(--panel-pad);
  padding-right: 0;
  background-color: var(--background-color-complement);
  transition: all 444ms ease-in 44ms;
}

.Layout-sidebar:before {
  content: attr(data-heading);
  position: absolute;
  top: .75rem;
  left: .75rem;
}

.Layout-sidebar:hover {
  width: var(--aside-width);
}


.Input {
  --input-height: 1.5rem;
  height: var(--input-height);
  padding: 0;
  margin: 0;
  outline: none;
  border: 0 none;
  border-radius: calc(var(--input-height) * .5);
  font: 1rem trench, sans-serif;
  color: var(--accent-color);
  background-color: transparent;
}


.Sequencer {
  --channel-height: 1.2rem;
  display: flex;
  align-items: stretch;
  height: -webkit-min-content;
  height: min-content;
}

.Sequencer-timeline {
  position: relative;
  overflow: hidden;
  flex-grow: 1;
}

.Sequencer-dots {
  --seq-dot-size: .4rem;
  position: absolute;
  top: -1.1rem;
  left: -.5rem;
  width: var(--seq-dot-size);
  height: var(--seq-dot-size);
  border-radius: calc(var(--seq-dot-size) * .5);
  background-color: var(--background-color);
  box-shadow:
    1rem 1.5rem 0 #333,
    3rem 3rem 0 #333,
    7rem 1.5rem 0 #333,
    11rem 3rem 0 #333,
    17rem 1.5rem 0 #333;
}

.Sequencer-tape {

  /* todo - port whatever is required from this block,
  and think of another way of representing the rolling tape */
  display: none;

  --seq-ticks: 2;
  /* dividing by 2 as the container width is 200% */
  --seq-tick-beat: calc((100% / var(--seq-ticks)) / 2);
  --seq-tick-bar: calc(var(--seq-tick-beat) * 4);
  --seq-tick-quarter: calc(var(--seq-tick-beat) / 4);
  --seq-tick-beat-border: calc(var(--seq-tick-beat) - .1rem);
  --seq-tick-bar-border: calc(var(--seq-tick-bar) - .2rem);
  --seq-tick-quarter-border: calc(var(--seq-tick-quarter) - .05rem);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 100%;
  width: 200%;
  background-color: var(--background-color-complement);
}

@media (min-width: 200px) { .Sequencer-tape { --seq-ticks: 4 } }
@media (min-width: 400px) { .Sequencer-tape { --seq-ticks: 8 } }
@media (min-width: 600px) { .Sequencer-tape { --seq-ticks: 12 } }
@media (min-width: 800px) { .Sequencer-tape { --seq-ticks: 16 } }
@media (min-width: 1000px) { .Sequencer-tape { --seq-ticks: 20 } }
@media (min-width: 1200px) { .Sequencer-tape { --seq-ticks: 24 } }
@media (min-width: 1400px) { .Sequencer-tape { --seq-ticks: 28 } }
@media (min-width: 1600px) { .Sequencer-tape { --seq-ticks: 32 } }

.Sequencer-tape:hover {
  animation: slideLeft 4s linear reverse infinite;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

.Sequencer-tape:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image:
    repeating-linear-gradient(
      90deg,
      transparent,
      transparent var(--seq-tick-quarter-border),
      var(--background-color) var(--seq-tick-quarter-border),
      var(--background-color) var(--seq-tick-quarter)
    ),
    repeating-linear-gradient(
      90deg,
      transparent,
      transparent var(--seq-tick-beat-border),
      var(--background-color) var(--seq-tick-beat-border),
      var(--background-color) var(--seq-tick-beat)
    ),
    repeating-linear-gradient(
      90deg,
      transparent,
      transparent var(--seq-tick-bar-border),
      var(--background-color) var(--seq-tick-bar-border),
      var(--background-color) var(--seq-tick-bar)
    );
}

.Sequencer-channels {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.Sequencer-channels .Sequencer-channel {
  height: var(--channel-height);
}

.Sequencer-channels .Sequencer-channel .Sequencer-tick {
  -webkit-appearance: none;
          appearance: none;
  width: calc(100% / var(--ticks-count));
  height: 100%;
  border-radius: calc(var(--channel-height) * .25);
  border: calc(var(--channel-height) * .1) solid var(--background-color);
  background-color: var(--background-color-complement);
  cursor: pointer;
}

.Sequencer-channels .Sequencer-channel .Sequencer-tick:hover,
.Sequencer-channels .Sequencer-channel .Sequencer-tick:checked {
  background-color: var(--accent-color);
}

.Neumorphism {
  --neumorphism-bg-color: var(--background-color-complement);
  --neumorphism-shadow-color-tint: var(--background-color-tint);
  --neumorphism-shadow-color-shade: var(--background-color-shade);

  --neumorphism-shadow-offset: .0375rem;
  --neumorphism-shadow-blur: calc(var(--neumorphism-shadow-offset) * 10);
  --neumorphism-shadow-spread: calc(var(--neumorphism-shadow-offset) * -.5);

  background-color: var(--neumorphism-bg-color);
  box-shadow:
    calc(var(--neumorphism-shadow-offset) * -1)
    calc(var(--neumorphism-shadow-offset) * -1)
    var(--neumorphism-shadow-blur)
    var(--neumorphism-shadow-spread)
    var(--neumorphism-shadow-color-tint),
    var(--neumorphism-shadow-offset)
    var(--neumorphism-shadow-offset)
    var(--neumorphism-shadow-blur)
    var(--neumorphism-shadow-spread)
    var(--neumorphism-shadow-color-shade);
}

.Transport-button {
  --transport-button-size: 2.6rem;
  --transport-icon-size: calc(var(--transport-button-size) * .333);

  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: var(--transport-button-size);
  height: var(--transport-button-size);
  -webkit-margin-end: var(--transport-icon-size);
          margin-inline-end: var(--transport-icon-size);
  outline: none;
  border: 0 none;
  border-radius: calc(var(--transport-button-size) * .5);
  background-color: var(--background-color-complement);
  cursor: pointer;
}

.Transport-button:last-of-type {
  -webkit-margin-end: 0;
          margin-inline-end: 0;
}

.Transport-button:after {
  --transport-button-icon-color: var(--background-color);

  content: '';
  display: block;
  width: var(--transport-icon-size);
  height: var(--transport-icon-size);
  border-style: solid;
  /* faux icon fill color */
  border-color: var(--transport-button-icon-color);
}

.Transport-button:hover:after {
  --transport-button-icon-color: var(--accent-color);
}

.Transport-button.Transport-button-play:after {
  --transport-icon-size-play: calc(var(--transport-icon-size) * 1.1);
  width: var(--transport-icon-size-play);
  height: var(--transport-icon-size-play);
  margin-right: -20%;
  border-width: calc(var(--transport-icon-size-play) * .5)
                calc(var(--transport-icon-size-play) * .2)
                calc(var(--transport-icon-size-play) * .5)
                calc(var(--transport-icon-size-play) * .8);
  border-top-color: transparent;
  border-right-color: transparent;
  border-bottom-color: transparent;
}

.Transport-button.Transport-button-stop:after {
  --transport-icon-size-stop: calc(var(--transport-icon-size) * .9);
  width: var(--transport-icon-size-stop);
  height: var(--transport-icon-size-stop);
  background-color: var(--transport-button-icon-color);
}

.Transport-button.Transport-button-record:after {
  border-radius: calc(var(--transport-icon-size) * .5);
  border-width: calc(var(--transport-icon-size) * .5);
}

.Transport {
  display: flex;
  align-items: stretch;
  justify-content: center;
  padding: .5rem;
}


.Meter {
  opacity: .5;
}

.Meter:not(:hover) {
  -webkit-filter: grayscale();
          filter: grayscale();
}


.Crossfader {
  flex-grow: 1;
  opacity: .5;
}

/* todo - extract this to a hoc for reuse */
.Crossfader:not(:hover) {
  -webkit-filter: grayscale();
          filter: grayscale();
}

.Visualizations {
  display: flex;
  align-items: center;
  justify-content: stretch;
  align-self: flex-end;
  padding-top: 0;
  padding-bottom: 0;
}

.Visualizations :not(:last-child) {
  -webkit-margin-end: .5rem;
          margin-inline-end: .5rem;
}

.Visualizations .Visualization-tight,
.Visualizations .Visualization-loose {
  flex-grow: 1;
  min-height: 2rem;
  background-color: transparent;
}

.Visualizations .Visualization-tight {
}

.Visualizations .Visualization-loose {
}


.MainView {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

.MainView > :not(:last-child) {
  -webkit-margin-after: .75rem;
          margin-block-end: .75rem;
}

.MainView-title {
  margin: 0;
  font-size: 4rem;
  letter-spacing: -.05em;
  font-weight: 100;
  text-align: right;
  color: var(--accent-color);
}

.MainView-panel,
.MainView-panel-center,
.MainView-panel-end {
  width: calc(100vw - (var(--panel-pad) * 2) - var(--aside-width));
  overflow: hidden;
  padding: .3rem;
  border: .125rem solid var(--accent-color);
  border-radius: .6rem;
}

.MainView-panel-end {
  align-self: flex-end;
}

.MainView-panel-center {
  align-self: center;
}


.Label {
  display: block;
  line-height: 1.5;
  white-space: nowrap;
}


.Select,
.Select .Select-option {
  height: var(--select-height);
  outline: none;
  border: 0 none;
}

.Select {
  --select-height: 1.5rem;
  -webkit-padding-start: .5em;
          padding-inline-start: .5em;
  -webkit-padding-end: 0;
          padding-inline-end: 0;
  border-radius: calc(var(--select-height) * .5);
  font: 1rem trench, sans-serif;
  color: var(--accent-color);
  background-color: var(--background-color-complement);
}


.NumberInput {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  -webkit-padding-start: .5rem;
          padding-inline-start: .5rem;
  -webkit-padding-end: 0;
          padding-inline-end: 0;
}

.Settings {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 2.4rem .4rem;
}

.Settings > * {
  -webkit-margin-after: .5rem;
          margin-block-end: .5rem;
}

.Settings :not(:last-child) {
  -webkit-margin-end: 1.5rem;
          margin-inline-end: 1.5rem;
}

.Settings .Setting-reset-interval {
  white-space: nowrap;
}

.Settings .Setting-reset-interval :not(:last-child) {
  -webkit-margin-end: .1rem;
          margin-inline-end: .1rem;
}


:root {
  font-size: calc(16px + .5vmax);
}

@font-face {
  src:  url(/drifter/static/media/trench100.21489afc.ttf) format('truetype'),
        url(/drifter/static/media/trench100.f4fa94b7.otf) format('opentype');
  font-family: trench;
  font-weight: 700;
}


:root {
  --background-color: #4a4a4a;
  --background-color-tint: #6a6a6ab0;
  --background-color-shade: #2a2a2ab0;
  --background-color-complement: #5c5c5c;
  --background-color-complement-tint: #7c7c7cb0;
  --background-color-complement-shade: #3c3c3cb0;
  --foreground-color: #ccc;
  --accent-color: #a3aae5;
}

@media (prefers-reduced-motion: no-preference) {

@-webkit-keyframes slideLeft {
  from { left: 0; right: 100%; }
  to { left: -100%; right: 0; }
}
@keyframes slideLeft {
  from { left: 0; right: 100%; }
  to { left: -100%; right: 0; }
}

}

/* make animations available only if the user enabled animations in the OS settings */

